<template>
  <div class="container">
    <div class="row">
      <div class="col-xs-12">
        <br>
        <h1>Checkout</h1>
      </div>
    </div>
    <hr>
    <router-link :to="{ name: 'merchant', params: { merchantId: 'tgnus' } }">
      <div class="row">
        <div class="col-xs-3">
          <img class="img-fluid" src="https://via.placeholder.com/220" />
        </div>
        <div class="col-xs-9">
          <h3><b>Thirsty Guys</b></h3>
          <h5>Faculty of Science S16 NUS</h5>
        </div>
      </div>
    </router-link>
    <hr>
    <div class="row">
      <div class="col-xs-6">
        Eat in / Take out
        <select class="form-control" v-model="collectionMethod" required>
          <option value="-1">-- Select --</option>
          <option value="eatIn">Eat in</option>
          <option value="takeOut">Take out</option>
        </select>
      </div>
      <div class="col-xs-6" v-if="collectionMethod == 'takeOut'">
        I am coming in ...
        <select class="form-control" v-model="collectionTime" required>
          <option value="-1">-- X minutes --</option>
          <option value="0">Now</option>
          <option value="5">5 minutes</option>
          <option value="10">10 minutes</option>
          <option value="15">15 minutes</option>
          <option value="30">30 minutes</option>
        </select>
      </div>
      <div class="col-xs-6" v-if="collectionMethod == 'eatIn'">
        Table number
        <input type="text" class="form-control" v-model="tableNumber" placeholder="#" required/>
      </div>
    </div>
    <hr>
    <div class="row">
      <div class="col-xs-12">
        <h4>Items</h4>
          <table class="table table-sm">
            <thead>
              <tr>
                <th scope="col-6">Item</th>
                <th scope="col=4">Add on</th>
                <th scope="col-2">Total</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, idx) in currentCart.items" :key="idx" class="cartItem cellPadded">
                <td><b @click="removeItem(idx)"><i class="ion-android-cancel text-warning"></i></b>&nbsp;&nbsp; <b>{{ item.name }}</b></td>
                <td>
                  <span v-for="(customisationItem, idx2) in item.customisationApplied" :key="idx2">
                    + {{ customisationItem }}
                    <br>
                  </span>
                </td>
                <td class="text-xs-right">SGD {{ item.price | 2decimal }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr class="cellPadded">
                <td colspan="2" class="text-xs-right">Subtotal</td>
                <td class="text-xs-right">SGD 20</td>
              </tr>
              <tr v-if="!validPromo" class="cellPadded">
                <td colspan="2" class="text-xs-right">Discount ( 20% )</td>
                <td class="text-xs-right">SGD 20</td>
              </tr>
              <tr class="cellPadded">
                <td colspan="2" class="text-xs-right">
                  <div class="row">
                    <div class="col-xs-7 align-middle text-success" v-if="validPromo">
                      Promo description
                    </div>
                    <div class="col-xs-7 align-middle text-success" v-if="!validPromo">
                      Apply promo code
                    </div>
                    <div class="col-xs-7 align-middle" v-if="isPromoLoading">
                      Loading promo code
                    </div>
                    <div class="col-xs-5 form-group">
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Enter promo code here"
                        v-model="promoCode"
                        v-if="!validPromo"
                      />
                    </div>
                  </div>
                </td>
                <td class="text-xs-right">SGD 20</td>
              </tr>
              <tr class="cellPadded">
                <td colspan="2" class="text-xs-right">Incl. GST</td>
                <td class="text-xs-right">SGD 20</td>
              </tr>
              <tr class="font-weight-bold cellPadded">
                <td colspan="2" class="text-xs-right">Total</td>
                <td class="text-xs-right">SGD 20</td>
              </tr>
            </tfoot>
          </table>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="submitOrder">Submit</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { REMOVE_FROM_CART } from "@/store/mutations.type";

export default {
  name: "viewCart",
  data() {
    return {
      collectionMethod: '-1',
      collectionTime: '-1',
      tableNumber: '',
      promoCode: '',
      isPromoLoading: false,
      validPromo: false,
      // validPromo: {
      //   promoId: '',
      //   promoName: '',
      //   conditions: [
          
      //   ],
      //   value: {
      //     type: 'BOGO(Cheaper) / value / percentage',
      //     effect: 'cart / product',
      //     amount: '',
      //   },
      // },
    };
  },
  computed: {
    ...mapGetters(["currentCart"])
  },
  methods: {
    removeItem(idx) {
      let item = this.currentCart.items[idx]
      
      if (window.confirm('Proceed removing ' + item.name + ' from your cart?')) {
        this.$store.commit(REMOVE_FROM_CART, idx)
      }else{
        return 0;
      }
    },

    calcTotalItemPrice (item) {
      return 0;
    },

    calcTotalOrder () {

    },

    calcSubTotalOrder () {

    },

    submitOrder () {
      if(this.collectionMethod == 'eatIn'){
        if(this.tableNumber.length < 1){
          alert('Please provide valid table number')
          return
        }else{
          // Proceed
        }
      }else if(this.collectionMethod == 'takeOut'){
        if([0, 5, 10, 15, 30].includes(parseInt(this.collectionTime))){
          // Proceed
        }else{
          alert('Please select pick-up time')
          return
        }
      }else{
        alert('Please select collection method')
        return
      }

      alert('Processing order...')
      this.$router.push({ name: "orderDetails", params: { 'orderId': 1} })
    }
  }
};
</script>

<style scoped>
.cartItem{
  font-size: 125%
}
tr.cellPadded > td  {
  padding: 15px 0; 
}
</style>


