<template>
	<div class="container">
		<div v-if="isPageLoading">
			<div class="col-xs-12">
				Loading {{ merchant.name }}'s data
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div class="col-xs-12">
				<img class="img-fluid" :src="merchant.image" />
				<h2><b>{{ merchant.name }}</b></h2>
				<p>{{ merchant.address }}</p>
				<hr>
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div class="col-xs-4" v-bind:class="{ mainColor: isMenuSelected }" @click="changeView('MENU')">
				<h4 class="text-xs-center font-weight-bold">MENU</h4>
			</div>
			<div class="col-xs-4" v-bind:class="{ mainColor: isAboutSelected }" @click="changeView('ABOUT')">
				<h4 class="text-xs-center font-weight-bold">ABOUT</h4>
			</div>
			<div class="col-xs-4" v-bind:class="{ mainColor: isLoyaltySelected }" @click="changeView('LOYALTY')">
				<h4 class="text-xs-center font-weight-bold">LOYALTY</h4>
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div class="col-xs-12" v-if="isMenuSelected">
				<hr class="mb-0">
				<div classs="categoryList" v-for="(category, idx) in merchant.menu" :key="idx">
					<MenuCategory class="menuCategory" :menuCategory='category'></MenuCategory>
					<hr class="margin0">
				</div>
			</div>
			<div class="col-xs-12" v-if="isAboutSelected">
				<hr>
				<p class="twentySizePtHeight">
					The iconic lifestyle destination that is Monti has landed at Fullerton Bay, bringing the best of food, drink and entertainment to the shores of Singapore. Set in the renowned spherical Fullerton Pavilion, Monti offers a holistic experience, combining eclectic modern Italian-influenced cuisine with tantalising drinks, presented in a luscious ambience with energetic DJ spins late into the night.<br>
From the kitchen comes multi-regional Italian cuisine, inspired by fresh produce that captures the essence of Italy's diverse culture. Go for the Squid Ink Tagliolini, a good representative of the flavour profile that Head Chef Felix Chong has specially concocted for Monti. The housemade pasta comes served with Kaluga Queen Caviar, juicy Fine De Claire Oysters and Sardinia Bottarga - otherwise known as the truffle of the sea. It's also balanced out by the sweetest Roma tomatoes and topped with creamy Hokkaido Sea Urchin.<br>
Other favourites include the 1kg Charcoal Grilled Sanchoku Wagyu Prime OP Rib. Served on a sizzling hotplate and emanating a glorious aroma, it is bound to ignite envious stares as it makes its way to your table. Drinks wise, the cocktails at Monti range from classic concoctions to bespoke tipples, making them the perfect companions to a hearty meal.<br>
Great vibes, amazing food, and a view to knock your socks off - we can't think of a better way to spend an evening. (Jan 2018)
				</p>
			</div>
			<div class="col-xs-12" v-if="isLoyaltySelected">
				<hr>
				<br>
				<h3>
					You are 6 stamps away from free boba
				</h3>
			</div>
		</div>
	</div>
</template>

<script>
import { FETCH_MERCHANT } from "@/store/actions.type";
import { mapGetters } from "vuex";
import store from "@/store";

import MenuCategory from "@/components/MenuCategory";

export default {
  name: "viewMerchant",
  components: {
    MenuCategory
  },
  data() {
	  return {
			isPageLoading: false,
			isMenuSelected: true,
			isAboutSelected: false,
			isLoyaltySelected: false,
	  }
	},
	methods: {
		changeView (viewName) {
			this.isMenuSelected = false;
			this.isAboutSelected = false;
			this.isLoyaltySelected = false;

			if(viewName == "MENU"){
				this.isMenuSelected = true;

			}if(viewName == "ABOUT"){
				this.isAboutSelected = true;

			}if(viewName == "LOYALTY"){
				this.isLoyaltySelected = true;
			}

		}
	},
  computed: {
    ...mapGetters(["merchant"])
	},
	// props: {
	// 	merchant: {
	// 		type: Object,
	// 		required: true
	// 	}
	// },
  beforeRouteEnter(to, from, next) {
    Promise.all([
			store.dispatch(FETCH_MERCHANT, to.params.merchantId),
    ]).then(() => {
      next();
    });
  },
}
</script>

<style scoped>
h2{
	margin-top: 10px;
}
.mainColor{
	color: #ffba00;
}
p{
	margin-top: -10px;
}
.margin0{
	margin:0
}
.mb-0{
	margin-bottom: 0;
}
.menuCategory{
	margin-top: 5px;
}
.twentySizePtHeight{
	line-height: 26pt;
}
</style>


