<template>
  <div>
    <div class="row" @click="selectItemCustomiserItem(customiser)">
      <div class="col-xs-9">
        <h5>
          <i class="ion-android-checkbox mainColor" v-if="isSelected"></i><i class="ion-android-checkbox-outline-blank mainColor" v-if="!isSelected"></i>&nbsp;&nbsp;&nbsp;{{ customiser.name }}
        </h5>
      </div>
      <div class="col-xs-3 text-xs-right">
        <h5>
          <small>{{ customiser.price | currency }}</small>
        </h5>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ItemCustomiseCategory",
  props: {
    customiser: {
      type: Object,
      required: true
    },
  },
  data () {
    return {
      isSelected: false,
    }
  },
  methods: {
    selectItemCustomiserItem (customiser) {
      this.isSelected = !this.isSelected
      // Check if category already fulfilled
    }
  }
};
</script>

<style scoped>
.mainColor{
  color: #ffba00;
}
</style>


