<template>
  <div class="container">
    <br>
    <div class="row">
      <div class="col-xs-12">
        <b class="ion-android-arrow-back mainColor"></b> {{ merchant.name }} / {{ merchant.address }}
      </div>
    </div>
    <div class="row">
      <div class="col-xs-9">
        <h1>{{ item.name }}</h1>
      </div>
      <div class="col-xs-3 text-xs-right">
        <h4><b>SGD {{ item.price }}</b></h4>
      </div>
    </div>
    <hr>
    <br>
    <div class="row" v-for="(custom, idx) in item.customisation" :key="idx">
      <div class="col-xs-12">
        <ItemCustomiseCategory :customObject='custom'></ItemCustomiseCategory>
        <hr v-if="idx != item.customisation.length - 1">
        <br>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12">
        <br>
        <button type="button" class="btn btn-primary btn-lg btn-block" @click="addItem">Submit</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import { ADD_TO_CART } from "@/store/mutations.type";

import ItemCustomiseCategory from "@/components/ItemCustomiseCategory";

export default {
  name: "viewItemCustomise",
  data() {
    return {
    };
  },
  components: {
    ItemCustomiseCategory
  },
  computed: {
    ...mapGetters(["merchant"]),
    item () {
      // console.log(this.merchant.menu.find(menuCategory => menuCategory.menuList.find(item => item.id === this.$route.params.itemId)).menuList.find(item => item.id === this.$route.params.itemId))
      return this.merchant.menu.find(menuCategory => menuCategory.menuList.find(item => item.id === this.$route.params.itemId)).menuList.find(item => item.id === this.$route.params.itemId);
    }
  },
  methods: {
    addItem () {
      // TODO: Form validation required

      let _item = this.item
      _item.customisationApplied = [ 'hello' ]

      console.log(_item)

      let toast = this.$toasted.show('<b>' + _item.name.toUpperCase() + ' </b>&nbsp;added to your cart', { 
        theme: "bubble", 
        position: "bottom-center", 
        duration : 2000
      });
      
      
      this.$store.commit(ADD_TO_CART, _item);
      this.$router.push({ name: 'merchant', params: { merchantId: this.merchant.id }})
    }
  }
}
</script>


<style scoped>
.align-text-bottom{
  vertical-align: bottom !important;
}
.mainColor{
  color: #ffba00;
}
</style>


